.about_section {
  width: 100%;

  background-color: #00ff00;
}

.about_section_inner {
  width: 90%;
  margin: auto;
  max-width: 1247px;
  display: flex;
  padding: 2% 0;
}
.about_heading {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about_heading > h1 {
  font-size: 3rem;
}
.about_description {
  width: 68%;
  display: flex;
  flex-direction: column;
}
.about_desc_icons {
  display: flex;
  align-items: center;
  gap: 2%;
  font-size: 1.2em;
}
