.navbar_a_main_div {
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  max-width: 1247px;
  margin: auto;
  align-items: center;
  padding-top: 1%;
  padding-bottom: 1%;
}
.navbar_part_a {
  width: 3%;
  right: 100%;
  top: 0;
}

.navbar_part_b {
  width: 50%;
  display: flex;
  gap: 2%;
  /* border: 1px solid red; */
  align-items: center;
  justify-content: space-around;
}
.navbar_part_b > div > a {
  text-decoration: none;
  font-size: 0.9em;
  color: #00ff00;
}

.navbar_part_b > div > a {
  display: inline-block;
  padding-bottom: 5px;
  position: relative;
}
.navbar_part_b > div > a::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  color: #00ff00;
  background-color: #00ff00;
  transition: width 0.65s ease-out;
}
.navbar_part_b > div > a:hover::before {
  width: 100%;
}
