.hero_home {
  width: 90%;
  max-width: 1247px;
  margin: auto;
  margin-top: 200px;
  /* border: 1px solid orange; */
  margin-bottom: 100px;
  align-items: center;
}

.hero_home_main {
  display: grid;
  align-items: center;
  justify-content: space-between;
  grid-template-columns: repeat(2, 1fr);
  width: 90%;
  /* border: 1px solid red; */
  margin: auto;
  gap: 1%;
}
.hero_home_intro {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  /* border: 1px solid red; */
}

.intro_name {
  color: #00ff00;
  font-size: 1.2em;
  margin-bottom: -20px;
}
.intro_title {
  color: #e0e7ed;
  font-size: 1.9em;
}
.about_me_btn {
  background-color: #00ff00;
  width: 190px;
  height: 50px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  margin-top: -10px;
}
.about_me_btn > p {
  font-size: medium;
}
.profile_picture {
  width: 1fr;
  margin: auto;
  /* border: 1px solid red; */
  display: flex;
  flex-direction: column;
  align-items: center;
}
.profile_picture > div:nth-child(1) > img {
  width: 45%;
  margin: auto;
}
.pfp {
  display: flex;
  width: 100%;
}
.social_app_icons {
  display: flex;
  align-items: center;
  width: 40%;
  margin: auto;
  gap: 3%;
  color: #00ff00;
  margin-top: 40px;
  justify-content: space-between;
  /* border: 1px solid red; */
}

/* MEDIA QUERY */
@media screen and (min-width: 1025px) {
}
@media screen and (max-width: 1024px) and (min-width: 481px) {
  .hero_home_main {
    grid-template-columns: repeat(1, 1fr);
  }
  .hero_home_intro {
    align-items: center;
  }
  .profile_picture > div:nth-child(1) > img {
    width: 20%;
  }
}
